.button {

  &--create {
    @media (max-width: 460px) {
      align-self: flex-end;
      height: 40px;
    }
  }

  &--remove,
  &--add {
    flex-shrink: 0;
    width: 32px;
    height: 26px;
    padding: 0;
  }

  &--remove {
    background-color: #e1e1e1;
    border: none;
  }

  &--add {
    height: 32px;

    font-size: 24px;
    line-height: 32px;
  }
}