html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  color: $color-main;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: $font-size;
  line-height: $line-height;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}