.card-item {
  max-width: 320px;
  width: 320px;
  border: 1px solid $grey;

  @media (max-width: 1199px) {
    max-width: 280px;
  }

  @media (max-width: 991px) {
    max-width: 200px;
  }

  @media (max-width: 499px) {
    max-width: none;
    width: 100%;
    margin-bottom: 24px;
  }
}